.TopBar {
  position: fixed;
  display: flex;
  top: 0;
  left: 0%;
  right: 0%;
  width: 100%;
  z-index: 100;
  font-size: 2ch;
  padding: 16px;
  justify-content: center;
  background-color: rgba(5, 20, 60, 0.5);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
}

.TopBar a {
  padding: 14px;
  text-decoration: none;
  border-radius: 20px;
  color: rgba(251, 251, 251, 0.921);
}

.TopBar a:hover {
  color: lightgreen;
}

.sep TopBar {
  width: 100%;
}
